<template>
    <div>
        <div class="col-md-11 p-2 form-row  mb-4 ml-5">
            <div class="col-md-1"><img src="@/assets/images/icons/user.png" class="mr-2 mt-5" /></div>
            <div class="col-md-11" :class="{'form-group--error':$v.comments.$error}">
                <p>Comments</p>
                <textarea v-model="$v.comments.$model" class="form-control mb-2" rows="4"></textarea>
                <div v-if="commentError">
                    <span class="text-danger" v-if="!$v.comments.required"> Please provide the comments</span>
                </div>
            </div>
        </div>

        <div class="comment-buttons">
            <button class="btn btn-primary pl-5 mr-2 pr-5" style="float:right;" @click="Approve">Approve</button>
            <button class="btn btn-outline-primary pl-5 pr-5 mr-2 p-2" style="float:right;" @click="Reject">Reject</button>
            <router-link class="btn btn-outline-primary mr-2 p-2 pl-5 pr-5" style="float:right;" to="/MfListing"><i class="fa fa-arrow-left"></i> Back</router-link>
        </div>
    </div>
</template>
<script>
    import axios from 'axios'
    import { required } from 'vuelidate/lib/validators';
    import toast from '@/toast';
    export default {
        data() {
            return {
                comments:'',
                commentError:false
            }
        },
        validations:{
            comments:{required}
        },        
        props:['mfId'],
        methods: {
            Approve: async function () {
                await axios.post(process.env.VUE_APP_VmApisBaseUrl + "/Manufacturer/AproveMf", { mfId: this.mfId, comments: this.comments }).then((resp) => {
                    if (resp.status === 200) {
                        toast.success("Manufacturer Approved!..");
                        this.$router.push({ name: "MfListing" });
                    }
                })
            },
            Reject: async function () {
                this.$v.$touch();
                this.commentError = this.$v.comments.$anyError;
                if (!this.commentError) {
                    await axios.post(process.env.VUE_APP_VmApisBaseUrl + "/Manufacturer/RejectMf", { mfId: this.mfId, comments: this.comments }).then((resp) => {
                        if (resp.status === 200) {
                            toast.success("Manufacturer Rejected!..");
                            this.$router.push({ name: "MfListing" });
                        }
                    })
                }
            }
        }
    }
</script>